
function saveLocal(value){
    return localStorage.setItem('shoppingCart',JSON.stringify(value))
}
const CartProduct={
    namespaced:true,
    state:{
        menu:localStorage.getItem('shoppingCart') ? JSON.parse(localStorage.getItem('shoppingCart')) : []
    },
    getters:{
    showProduct(state){
        return state.menu;
    },
        calcProduct(state){
       return state.menu.reduce((total,items)=>{
            return total + (items.quantity * items.price)
        },0)
        }
    },
    mutations:{
        SetProduct(state,data){
           const myProduct=state.menu.find(items=>items.id===data.id)
            if(!myProduct){
                state.menu.push({
                    ...data,
                    quantity:1
                })
            }else{
                myProduct.quantity++
            }
            saveLocal(state.menu)
        },
        addPro(state,data){
            let addProducts=state.menu.find(items=>items.id === data)
            if(addProducts){
                addProducts.quantity++
            }
            saveLocal(state.menu)
        },
        minesPro(state,data){
            let minesProducts=state.menu.find(items=>items.id===data)
            if(minesProducts){
                if(minesProducts.quantity > 1){
                    minesProducts.quantity--
                }
            }
            saveLocal(state.menu)
        },
        deletePro(state,data){
            state.menu=state.menu.filter(items=>items.id !== data)
            saveLocal(state.menu)
        }
    },
    actions:{
        plusProduct({commit},payload){
            commit('SetProduct',payload)
        },
       addMyProduct({commit},payload){
           commit('addPro',payload)
       },
        minesMyProduct({commit},payload){
            commit('minesPro',payload)
        },
        deleteAllProduct({commit},payload){
            commit('deletePro',payload)
        }
    }
}
export default CartProduct;